
    import { defineComponent, PropType } from "vue";
    import { ISkill } from "@/data";

    export default defineComponent({
        name: "app-skill",
        data() {
            return {
                showDetails: false,
                randomGlob: "",
                interval: -1,
                canClick: true,
            };
        },
        props: {
            skill: { type: Object as PropType<ISkill>, required: true },
        },
        computed: {
            detailsClass(): any {
                return {
                    "skill-details": true,
                    show: this.showDetails,
                };
            },
            titleClass(): any {
                return {
                    "skill-title": true,
                    clickable: this.skill.skills && this.skill.skills.length,
                };
            },
        },
        methods: {
            handleClick(ev: MouseEvent) {
                if (this.canClick) this.showDetails = true;
            },
            handleKeydown(ev: KeyboardEvent) {
                ev.stopPropagation();
                if (ev.key === "Escape") {
                    this.showDetails = false;
                }
            },
            makeRandomGlob() {
                let path = "";
                let lastX = [50, 50];
                let lastY = [50, 50];
                let count = 0;
                for (let rad = 0; rad <= Math.PI * 2; rad += Math.PI / 16) {
                    const r = Math.random() * 10 - 5 + 45;
                    const x = Math.cos(rad) * r + 50;
                    const y = Math.sin(rad) * r + 50;
                    if (count == 0) {
                        path = `M${x} ${y}`;
                    } else if (count % 3 == 2) {
                        path += `S ${lastX[1]} ${lastY[1]} ${x} ${y}`;
                    }
                    ++count;
                    lastX[0] = lastX[1];
                    lastX[1] = x;
                    lastY[0] = lastY[1];
                    lastY[1] = y;
                }
                path += "Z";
                return path;
            },
            animationStart() {
                this.canClick = false;
            },
            animationEnd(ev: AnimationEvent) {
                console.debug(ev.type);
                this.canClick = true;
            },
        },
        watch: {
            showDetails(value) {
                if (value) {
                    history.pushState(null, this.skill.name);
                    document.addEventListener("keydown", this.handleKeydown);
                } else {
                    history.back();
                    document.removeEventListener("keydown", this.handleKeydown);
                }
            },
        },
        mounted() {
            this.randomGlob = this.makeRandomGlob();
            this.interval = setInterval(() => {
                this.randomGlob = this.makeRandomGlob();
            }, 1000);
        },
        beforeUnmount() {
            clearInterval(this.interval);
            document.removeEventListener("keydown", this.handleKeydown);
        },
    });
